.quiz-creator {
  display: flex;
  align-self: center;
  flex-direction: column;
  padding-top: 100px;
  flex-grow: 1;
  width: 100%;
  max-width: 600px;

  &__form {
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 0px 0px 10px 1px #fff;
    padding: 30px;
    border-radius: 5px;
  }
}

.quiz-creator h1 {
  text-align: center;
  margin-bottom: 30px;
}
