@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.layout{height:100vh;display:flex;flex-direction:column;background:linear-gradient(90deg, #4334ff, #b340ff)}.main-content{display:flex;flex-direction:column;flex-grow:1}

.menu-toggle{position:fixed;top:40px;left:40px;font-size:30px;cursor:pointer;transition:0.3s ease-in-out;z-index:100}.menu-toggle:hover{opacity:0.7}.menu-toggle.open{left:320px}

.drawer{position:fixed;top:0;left:0;bottom:0;display:flex;flex-direction:column;width:80%;max-width:300px;background-color:#fff;padding:40px 30px;transform:translateY(0px);transition:0.3s ease-in-out;z-index:90}.drawer.close{transform:translateX(-300px)}.drawer__item{margin-bottom:20px}.drawer .drawer__link{color:#4334ff;font-size:30px;cursor:pointer;transition:0.3s}.drawer__link:hover{opacity:0.7}.active{text-decoration:underline;font-weight:bold}

.backdrop{z-index:50;top:0;bottom:0;right:0;left:0;position:fixed;transition:0.3s;background-color:rgba(0,0,0,0.8)}

.quiz{display:flex;flex-direction:column;align-items:center;padding-top:100px;flex-grow:1;width:100%}

.active-quiz{margin-top:30px;max-width:800px;width:95%;padding:20px;border:2px solid #fff;border-radius:5px}.active-quiz__question{display:flex;align-items:center;justify-content:space-between}.active-quiz__question span{margin-left:10px;white-space:nowrap}

.answer-list{margin-top:20px;display:flex;flex-direction:column;justify-content:space-between}

.answer-item{border:1px solid #fff;border-radius:5px;padding:5px 10px;margin-bottom:10px;cursor:pointer}.answer-item:hover{background-color:rgba(255,255,255,0.2);transition:0.3s ease-in-out}.answer-item:last-child{margin-bottom:0}.answer-item.success{background:rgba(52,168,83,0.7);font-size:bold}.answer-item.error{background-color:rgba(234,67,53,0.7);font-size:bold}

.finished-quiz{margin-top:30px;max-width:800px;width:95%;padding:20px;border:2px solid #fff;border-radius:5px}.finished-quiz__list{margin-bottom:10px}.finished-quiz__item{margin-bottom:10px}.finished-quiz__result{display:flex;flex-direction:column;align-items:center;margin-top:20px}.finished-quiz__result p{font-size:24px;text-decoration:underline}.finished-quiz .finished-quiz__list li i{margin-left:10px}i.success{color:#34a853}i.error{color:#ea4335}.finished-quiz__btn{margin-top:20px}

.button{display:inline-block;padding:15px 30px;border-radius:5px;margin-right:15px;text-transform:uppercase;font-weight:bold;font-size:18px;color:#fff;cursor:pointer;transition:0.3s}.button:focus{outline:none}.button:active{box-shadow:2px 2px 5px 2px #87cefa}.button:hover{opacity:0.9}.button.error{background-color:#ea4335}.button.success{background-color:#34a853}.button.primary{background-color:#4285f4}.button:disabled{background:#ccc;opacity:0.6;cursor:not-allowed;color:#000}

.lds-dual-ring{display:inline-block;width:80px;height:80px}.lds-dual-ring:after{content:" ";display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:6px solid #fff;border-color:#fff transparent #fff transparent;-webkit-animation:lds-dual-ring 1.2s linear infinite;animation:lds-dual-ring 1.2s linear infinite}@-webkit-keyframes lds-dual-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes lds-dual-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.auth{padding-top:100px;display:flex;justify-content:center;flex-grow:1;width:100%}.auth__container{width:100%;max-width:600px;padding:0 20px}.auth__form{background-color:rgba(255,255,255,0.9);box-shadow:0px 0px 10px 1px #fff;padding:30px;border-radius:5px}.auth h1{text-align:center;margin-bottom:30px}

.input-container{display:flex;flex-direction:column;margin-bottom:10px}.input-container label{margin-bottom:5px;padding:0;display:block;font-weight:bold;color:#4334ff}.input-container input{display:block;outline:none;border:1px solid #bebebe;padding:7px;margin:0 0 5px;width:100%;transition:0.3s ease-in-out}.input-container span{color:#f01f30;font-weight:bold;font-size:16px}.input-container.invalid input{box-shadow:0px 0px 5px 0.5px #f01f30}

.quizes{display:flex;flex-direction:column;align-items:center;padding-top:100px;flex-grow:1;width:100%}.quizes__list{margin-top:30px;max-width:800px;width:95%;padding:20px;border:2px solid #fff;border-radius:5px}.quizes .quizes__list--item{margin-bottom:10px;padding:5px 10px;cursor:pointer}.quizes .quizes__list--item:hover{background-color:rgba(255,255,255,0.2);border-radius:5px}.quizes .quizes__list--item:last-child{margin-bottom:0}.quizes__list--link{color:#fff}.quizes__list--link:hover{opacity:0.7;transition:0.3s}

.quiz-creator{display:flex;align-self:center;flex-direction:column;padding-top:100px;flex-grow:1;width:100%;max-width:600px}.quiz-creator__form{background-color:rgba(255,255,255,0.9);box-shadow:0px 0px 10px 1px #fff;padding:30px;border-radius:5px}.quiz-creator h1{text-align:center;margin-bottom:30px}

.select-container{margin-bottom:15px}.select-container label{margin-bottom:5px;padding:0;display:block;font-weight:bold;color:#4334ff}select{display:block;outline:none;border:1px solid #bebebe;margin:0 0 5px;height:37px;width:100%;transition:all .3s ease-in-out}

*{box-sizing:border-box}html{height:100%}body{font-size:18px;color:#fff;font-family:"Open Sans", Helvetica, sans-serif}h1,h2,h3,h4,h5,h6{margin:0}h1{font-size:48px}h2{font-size:36px}p{margin:0;padding:0}ul{padding:0;margin:0;list-style:none}a{text-decoration:none}button{border:none;font-family:"Open Sans", Helvetica, sans-serif}input{font-size:18px;color:#4334ff}

